body {
  background-color: #1f042b;
  font-size: 16px;
}

section {
  flex-direction: column;
  display: flex;
}

a {
  color: #f1c7e7;
  text-align: center;
  text-transform: uppercase;
  background-color: #45446e;
  border: 4px solid #a15454;
  border-radius: 6px;
  padding: 5px 15px;
  text-decoration: none;
  transition: background-color .5s ease-in-out;
}

a:hover {
  color: #45446e;
  background-color: #f1c7e7;
  border: 4px solid #813939;
}

.container-home {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menu {
  width: 600px;
  color: #f08080;
  flex-direction: column;
  font-size: 35px;
  display: flex;
  position: relative;
}

#witch2 {
  width: 40px;
  height: 50px;
  background-image: url("witchM1.efde3ccb.png");
  background-size: 40px 50px;
  animation: wMove .5s linear 1s infinite;
  position: absolute;
  top: 126px;
  left: 80px;
}

@keyframes wMove {
  0% {
    background-image: url("witchM1.efde3ccb.png");
  }

  100% {
    background-image: url("witchM2.cf870433.png");
  }
}

.nav {
  flex-direction: column;
  gap: 30px;
  line-height: 1.5;
  display: flex;
}

.container-game {
  width: 900px;
  height: 600px;
  background-repeat: repeat-x;
  border: 2px solid #000;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 70px auto 40px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 22px 70px 4px rgba(0, 0, 0, .56);
}

.sky {
  width: 100%;
  height: 390px;
  background-image: linear-gradient(162deg, #1e0e47 39%, #b90c0c 98%, #fcb045 98.9%);
  position: relative;
}

#cloud {
  width: 138px;
  height: 40px;
  z-index: 1;
  background-color: #97848e;
  border-radius: 100px;
  margin: 20px 0 0;
  animation: skyMove 90s linear infinite;
  position: absolute;
  top: 30px;
  right: -140px;
}

#cloud:after, #cloud:before {
  content: "";
  background-color: #97848e;
  position: absolute;
}

#cloud:after {
  width: 40px;
  height: 38px;
  border-radius: 100px;
  bottom: 18px;
  left: 24px;
}

#cloud:before {
  width: 53px;
  height: 53px;
  border-radius: 50%;
  bottom: 15px;
  right: 27px;
}

@keyframes skyMove {
  0% {
    right: -140px;
  }

  100% {
    right: 960px;
  }
}

#witch {
  width: 40px;
  height: 50px;
  background-image: url("witchM1.efde3ccb.png");
  background-size: 40px 50px;
  position: absolute;
  bottom: 0;
  left: 50px;
}

.move {
  animation: wMove .5s linear infinite;
}

@keyframes wMove {
  0% {
    background-image: url("witchM1.efde3ccb.png");
  }

  100% {
    background-image: url("witchM2.cf870433.png");
  }
}

#fire {
  width: 33px;
  height: 38px;
  background-image: url("fire.18e8d059.png");
  background-repeat: no-repeat;
  animation: backgroundMove 2.535s linear infinite, fMove .5s linear 1s infinite;
  position: absolute;
  bottom: 0;
  right: -35px;
}

@keyframes fMove {
  0% {
    background-image: url("fire.18e8d059.png");
  }

  100% {
    background-image: url("fire2.307467ba.png");
  }
}

#arrow {
  width: 40px;
  height: 15px;
  background-image: url("arrow.bbff261e.png");
  background-repeat: no-repeat;
  animation: backgroundMove 2.05s linear 63s infinite;
  position: absolute;
  bottom: 9px;
  right: -50px;
}

#bat {
  width: 40px;
  height: 20px;
  background-image: url("bat.fe0645a5.png");
  background-repeat: no-repeat;
  background-size: 40px 20px;
  animation: backgroundMove 5.57s linear 4s infinite;
  position: absolute;
  bottom: 9px;
  right: -50px;
}

@keyframes backgroundMove {
  0% {
    right: -50px;
  }

  25% {
    right: 225px;
  }

  50% {
    right: 500px;
  }

  80% {
    right: 880px;
  }

  90% {
    right: 920px;
  }

  100% {
    right: 950px;
  }
}

.jump {
  animation: jump .7s linear;
}

@keyframes jump {
  0% {
    bottom: 0;
  }

  30% {
    bottom: 60px;
  }

  50% {
    bottom: 130px;
  }

  80% {
    bottom: 50px;
  }

  100% {
    bottom: 0;
  }
}

.ground {
  width: 100%;
  height: 210px;
  background: linear-gradient(1deg, #0d2414 42%, #393c11 95%, #797c11 99%);
}

.input-wrapper {
  width: 595px;
  position: relative;
  left: 40rem;
}

.range-label {
  width: 40px;
  height: 20px;
  background-image: url("bat.fe0645a5.png");
  background-repeat: no-repeat;
  background-size: 40px 20px;
  margin: 0;
  display: block;
  position: relative;
  top: 1rem;
  right: 4rem;
}

#range-bat {
  width: 540px;
  accent-color: #c70039;
  margin: 0 auto;
}

#rangevalue {
  width: 45px;
  height: 20px;
  text-align: center;
  background-color: #e9967a;
  border: 1px solid #fffaf0;
  border-radius: 4px;
  font-size: 18px;
  display: inline-block;
}

.go-back-btn {
  width: 400px;
  margin: 40px auto 0;
  padding: 5px 15px;
}

